import React from "react";
import Head from "next/head";
import Style from "./style.module.scss";
import { getHost, SITE_NAME } from "~/utils";
import { GiscusComment } from "~/components/GiscusComment";
import { Request } from "~/http/request";
import { withRouter } from "next/router";

class poetry extends React.Component {
  render() {
    const { data, router } = this.props;
    if (!data) {
      return null;
    }
    const postLink = getHost() + router.asPath;
    return (
      <div className={Style.detail_container}>
        <Head>
          <title key="title">
            {data.name} - {SITE_NAME}
          </title>
          <meta name="keywords" content={(data?.tags || []).join(",")} />
          <meta
            name="description"
            content={data.section || data.paragraphs?.[0]}
          />
          <meta property="og:title" content={`${data.name} - ${SITE_NAME}`} />
          <meta property="og:url" content={postLink} />
        </Head>
        <main className={Style.main}>
          <div className={Style.poetry}>
            <div>
              <h2 className={Style.name}>{data.name}</h2>
              <p className={Style.author}>作者: {data.author?.name}</p>
              <div>
                {data.paragraphs.map((row, index) => (
                  <div key={index} className={Style.row}>{row}</div>
                ))}
              </div>
            </div>
          </div>
          <div className={Style.ads}>
            <ins
              className="adsbygoogle"
              style={{
                display: "block",
                textAlign: "center",
                height: "100px",
              }}
              data-ad-layout="in-article"
              data-ad-format="fluid"
              data-ad-client="ca-pub-6887995008287565"
              data-ad-slot="2469914690"
            ></ins>
          </div>
          <section className={Style.comments}>
            <GiscusComment />
          </section>
        </main>
      </div>
    );
  }
}

export async function getStaticProps(context) {
  const id = context.params.id;
  // 获取当前文章
  const res = await Request.getChecklessPoetry(id);
  return {
    props: { data: res.data, id },
  };
}

export async function getStaticPaths() {
  const res = await Request.getPoetryIdList({
    pageNo: 0,
    pageSize: 10,
  });

  const list = res.data.data;
  const paths = list.map((item) => ({
    params: { id: item.id },
  }));

  return {
    paths,
    fallback: true, // false or 'blocking'
  };
}

export default withRouter(poetry);
